.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

.button {
  font-weight: 400;
  color: hsl(321deg, 100%, 78%);
  border: 1px solid hsl(321deg, 100%, 78%);
  border-radius: 3rem;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  text-align: center;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 2rem;
}

.header .header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 2rem;
}
@media (min-width: 1024px) {
  .header .header-content {
    padding: 4rem 2rem;
  }
}
.header .header-content .logo {
  width: 10rem;
}
@media (min-width: 1024px) {
  .header .header-content .logo {
    width: 15rem;
  }
}
.header .header-content .button {
  font-size: 1.2rem;
  padding: 0.5rem 2rem;
}

.community {
  margin: 8.5rem 0;
}
.community .community-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 2rem;
}
.community .community-content .text {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 2rem;
  margin: 0 0 5rem 0;
}
.community .community-content .text p {
  text-align: center;
  margin: 0 0 3.2rem 0;
}
@media (min-width: 1024px) {
  .community .community-content .text p {
    padding: 0 30rem;
  }
}
.community .community-content .text .button {
  padding: 1.2rem 5rem;
  font-weight: 600;
  background-color: hsl(322deg, 100%, 66%);
  color: hsl(207deg, 100%, 98%);
}
.community .community-content .imagen {
  display: inline-block;
  margin: 5rem 0 5rem 0;
  padding: 0 2rem;
}
.community .community-content .counters {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5rem 0 5rem 0;
}
@media (min-width: 1024px) {
  .community .community-content .counters {
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
  }
}
.community .community-content .counters .counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5rem 0;
}
.community .community-content .counters .counter .counter-logo {
  width: 3rem;
  align-self: flex-start;
}
.community .community-content .counters .counter .counter-number {
  font-family: "Open Sans" sans-serif;
  font-size: 6rem;
  font-weight: 700;
  margin: 0;
}
.community .community-content .counters .counter .counter-text {
  font-size: 1.8rem;
  color: #455a64;
}

.benefits {
  margin: 8.5rem 0;
}
.benefits .benefits-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 5rem 0;
}
.benefits .benefits-content .benefit {
  padding: 5rem 5rem;
  margin: 5rem 0;
  position: relative;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .benefit {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .benefits .benefits-content .benefit:nth-child(odd) {
    flex-direction: row-reverse;
  }
  .benefits .benefits-content .benefit .imagen,
.benefits .benefits-content .benefit .text {
    width: 50%;
  }
}
.benefits .benefits-content .benefit .imagen {
  margin-bottom: 6rem;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .benefit .imagen img {
    margin: 0 auto;
  }
}
.benefits .benefits-content .benefit .text p {
  text-align: center;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .benefit .text {
    padding: 0 7.5rem;
  }
  .benefits .benefits-content .benefit .text p, .benefits .benefits-content .benefit .text h1 {
    text-align: left;
  }
}
.benefits .benefits-content .together,
.benefits .benefits-content .users,
.benefits .benefits-content .conversation {
  width: 100%;
}
.benefits .benefits-content .together {
  background-color: hsl(207deg, 100%, 98%);
  position: relative;
}
.benefits .benefits-content .together::before {
  content: "";
  display: block;
  width: 100%;
  height: 112px;
  background: url("../img/bg-section-top-mobile-1.svg") no-repeat center top;
  background-size: 100% 112px;
  position: absolute;
  top: -112px;
  left: 0;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .together::before {
    background: url("../img/bg-section-top-desktop-1.svg") no-repeat center top;
  }
}
.benefits .benefits-content .together::after {
  content: "";
  display: block;
  width: 100%;
  height: 52px;
  background: url("../img/bg-section-bottom-mobile-1.svg") no-repeat center top;
  background-size: 100% 52px;
  position: absolute;
  bottom: -52px;
  left: 0;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .together::after {
    background: url("../img/bg-section-bottom-desktop-1.svg") no-repeat center top;
    height: 112px;
    bottom: -112px;
    background-size: 100% 112px;
  }
}
.benefits .benefits-content .users {
  background-color: hsl(207deg, 100%, 98%);
  position: relative;
}
.benefits .benefits-content .users::before {
  content: "";
  display: block;
  width: 100%;
  height: 112px;
  background: url("../img/bg-section-top-mobile-2.svg") no-repeat center top;
  background-size: 100% 112px;
  position: absolute;
  top: -112px;
  left: 0;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .users::before {
    background: url("../img/bg-section-top-desktop-2.svg") no-repeat center top;
  }
}
.benefits .benefits-content .users::after {
  content: "";
  display: block;
  width: 100%;
  height: 52px;
  background: url("../img/bg-section-bottom-mobile-2.svg") no-repeat center top;
  background-size: 100% 52px;
  position: absolute;
  bottom: -52px;
  left: 0;
}
@media (min-width: 1024px) {
  .benefits .benefits-content .users::after {
    background: url("../img/bg-section-bottom-desktop-2.svg") no-repeat center top;
    height: 112px;
    bottom: -112px;
    background-size: 100% 112px;
  }
}

.started {
  margin: 8.5rem 0;
}
@media (min-width: 1024px) {
  .started {
    margin: 15rem 0;
  }
}
.started .started-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0 8.5rem;
}
@media (min-width: 1024px) {
  .started .started-content {
    padding: 12.5rem 0;
  }
}
.started .started-content h1 {
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .started .started-content h1 {
    font-size: 3rem;
  }
}
.started .started-content .button {
  color: hsl(207deg, 100%, 98%);
  background-color: hsl(322deg, 100%, 66%);
  font-size: 1.4rem;
  font-weight: 600;
  padding: 1rem 3rem;
}
@media (min-width: 1024px) {
  .started .started-content .button {
    font-size: 1.6rem;
    padding: 2rem 6rem;
  }
}

.footer {
  background-color: hsl(192deg, 100%, 9%);
  position: relative;
}
.footer .footer-content {
  padding: 2rem 0 4rem 0;
}
@media (min-width: 1024px) {
  .footer .footer-content {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    padding: 8rem 0;
  }
}
.footer .footer-content .contact-footer {
  padding: 0 1.5rem;
  color: hsl(207deg, 100%, 98%);
}
@media (min-width: 1024px) {
  .footer .footer-content .contact-footer {
    width: 33.3%;
  }
}
.footer .footer-content .contact-footer p {
  color: hsl(207deg, 100%, 98%);
}
.footer .footer-content .contact-footer .contacts {
  padding: 1.5rem 0;
}
.footer .footer-content .contact-footer .contacts .contact {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}
.footer .footer-content .contact-footer .contacts .contact p {
  margin: 0;
}
.footer .footer-content .contact-footer .contacts .contact .logo {
  margin-right: 1rem;
}
.footer .footer-content .contact-footer .socials {
  padding-top: 3rem;
  display: flex;
  justify-content: flex-start;
}
.footer .footer-content .contact-footer .socials .social {
  cursor: pointer;
  padding-right: 1.5rem;
}
.footer .footer-content .contact-footer .socials .social i {
  font-size: 3rem;
}
@media (min-width: 1024px) {
  .footer .footer-content .newsletter {
    width: 33.3%;
  }
}
.footer .footer-content .newsletter .newsletter-content {
  background-color: hsl(192deg, 100%, 9%);
  padding: 0 2.5rem 5rem 2.5rem;
}
.footer .footer-content .newsletter .newsletter-content h1, .footer .footer-content .newsletter .newsletter-content p {
  color: hsl(207deg, 100%, 98%);
  text-align: left;
}
.footer .footer-content .newsletter .newsletter-content h1 {
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
}
.footer .footer-content .newsletter .newsletter-content .form {
  padding: 1.5rem 0;
}
.footer .footer-content .newsletter .newsletter-content .form .field {
  display: flex;
  justify-content: flex-end;
  min-width: 100%;
  margin-bottom: 1.5rem;
}
.footer .footer-content .newsletter .newsletter-content .form .field input[type=text] {
  width: 100%;
  padding: 1.5rem 1rem;
  border: none;
  border: 0.1rem solid hsl(207deg, 100%, 98%);
  border-radius: 1rem;
}
.footer .footer-content .newsletter .newsletter-content .form .field input[type=button] {
  font-weight: 600;
  padding: 1.5rem 2rem;
  background-color: hsl(322deg, 100%, 66%);
  color: hsl(207deg, 100%, 98%);
  align-self: flex-end;
  width: 50%;
  border: 0.1rem solid transparent;
  cursor: pointer;
  border-radius: 1rem;
}
.footer::before {
  content: "";
  display: block;
  width: 100%;
  height: 5.2rem;
  background: url("../img/bg-footer-top-mobile.svg") no-repeat center top;
  background-size: 100% 5.2rem;
  position: absolute;
  top: -5.1rem;
  left: 0;
}
@media (min-width: 1024px) {
  .footer::before {
    background: url("../img/bg-footer-top-desktop.svg") no-repeat center top;
    background-size: 100% 15.4rem;
    height: 15.4rem;
    top: -15.1rem;
  }
}